<template>
  <view class="uni-container">
    <uni-forms ref="form" :model="formData" validateTrigger="bind">
      <uni-forms-item name="edit_id" label="名称">
        <uni-data-picker v-model="formData.edit_id" collection="Manufacturer_brand" parent-field="parent_id.value"
          self-field="_id" field="_id as value, name as text"></uni-data-picker>
      </uni-forms-item>
      <uni-forms-item name="ram" label="内存" required>
        <uni-data-checkbox v-model="formData.ram" :localdata="formOptions.ram_localdata"></uni-data-checkbox>
      </uni-forms-item>
      <uni-forms-item name="rom" label="闪存" required>
        <uni-data-checkbox v-model="formData.rom" :localdata="formOptions.rom_localdata"></uni-data-checkbox>
      </uni-forms-item>
      <uni-forms-item name="money" label="价格">
        <uni-easyinput placeholder="价格表" v-model="formData.money"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="sort" label="排序">
        <uni-easyinput type="number" v-model="formData.sort"></uni-easyinput>
      </uni-forms-item>
      <view class="uni-button-group">
        <button type="primary" class="uni-button" style="width: 100px;" @click="submit">提交</button>
        <navigator open-type="navigateBack" style="margin-left: 15px;">
          <button class="uni-button" style="width: 100px;">返回</button>
        </navigator>
      </view>
    </uni-forms>
  </view>
</template>

<script>
  import {
    validator
  } from '../../js_sdk/validator/Mmoney.js';

  const db = uniCloud.database();
  const dbCmd = db.command;
  const dbCollectionName = 'Mmoney';

  function getValidator(fields) {
    let result = {}
    for (let key in validator) {
      if (fields.includes(key)) {
        result[key] = validator[key]
      }
    }
    return result
  }



  export default {
    data() {
      let formData = {
        "edit_id": "",
        "ram": null,
        "rom": null,
        "money": "",
        "sort": 0
      }
      return {
        formData,
        formOptions: {
          "ram_localdata": [{
              "value": 4,
              "text": 4
            },
            {
              "value": 6,
              "text": 6
            },
            {
              "value": 8,
              "text": 8
            },
            {
              "value": 12,
              "text": 12
            },
            {
              "value": 16,
              "text": 16
            },
            {
              "value": 18,
              "text": 18
            }
          ],
          "rom_localdata": [{
              "value": 32,
              "text": 32
            },
            {
              "value": 64,
              "text": 64
            },
            {
              "value": 128,
              "text": 128
            },
            {
              "value": 256,
              "text": 256
            },
            {
              "value": 512,
              "text": 512
            },
            {
              "value": 1024,
              "text": 1024
            }
          ]
        },
        rules: {
          ...getValidator(Object.keys(formData))
        }
      }
    },
    onReady() {
      this.$refs.form.setRules(this.rules)
    },
    methods: {

      /**
       * 验证表单并提交
       */
      submit() {
        uni.showLoading({
          mask: true
        })
        this.$refs.form.validate().then((res) => {
          return this.submitForm(res)
        }).catch(() => {}).finally(() => {
          uni.hideLoading()
        })
      },

      /**
       * 提交表单
       */
      submitForm(value) {
        // 使用 clientDB 提交数据
        return db.collection(dbCollectionName).add(value).then((res) => {
          uni.showToast({
            title: '新增成功'
          })
          this.getOpenerEventChannel().emit('refreshData')
          setTimeout(() => uni.navigateBack(), 500)
        }).catch((err) => {
          uni.showModal({
            content: err.message || '请求服务失败',
            showCancel: false
          })
        })
      }
    }
  }
</script>
